<template>
    <div class="wrap">
      <van-nav-bar
        title="申请记录"
        left-text="返回"
        left-arrow
        fixed
        @click-left="onClickLeft"
      />
      <div class="mainContent">
        <div class="item" v-for="(item, index) in list" @click="goTo(item)">
          <div class="left">
            <div class="name">{{item.applyName}}</div>
            <div class="time">{{item.applyTime}}</div>
          </div>
          <div class="right">
            <img src="../../../assets/images/mall/verify.png" alt="" v-if="item.auditState === 0">
            <img src="../../../assets/images/mall/success.png" alt="" v-if="item.auditState === 1">
            <img src="../../../assets/images/mall/failure.png" alt="" v-if="item.auditState === 2">
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    export default {
      name: "applyRecord",
      data() {
        return {
          list: [],
          token: '',

        }
      },
      methods: {
        onClickLeft(){
          this.$router.back();
        },
        // 获取申请授权列表
        getApplyList () {
          this.$http.post(backstageIp + userUrl + '/getFlagshipData',
            {
              url: cfaeIp + '/mall/audit/getAuditList.json?flag=0&pageNum=1&pageSize=1000',
              type: 'get',
              cookie: 'token=' + this.token
            }
          ).then(res => {
            let data = JSON.parse(res.data.data)
            this.list = data.data.result
            console.log(data.data.result);
          }).catch(err => {
            this.$toast('当前无网络连接或网络不稳定，请稍后重试');
          });
        },
        goTo (e) {
          console.log(e)
          let data = {}
          data = e
          sessionStorage.setItem('data', JSON.stringify(data))
          this.$router.push({name:'informationDetail'})
        }
      },
      created() {
        this.token = sessionStorage.getItem('token');
      },
      mounted() {
        this.getApplyList ()
      }
    }
</script>

<style scoped>
  .mainContent {
    margin-top: 46px;
    padding-left: 15px;
    min-height: calc(100vh - 46px);
    box-sizing: border-box;
  }
.item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #eee;
}
  .left {
    display: flex;
    flex-direction: column;
  }
  .name {
    margin-bottom: 8.5px;
    font-size:14px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(32,32,32,1);
    line-height:20px;
  }
  .time {
    font-size:12px;
    font-family:PingFangSC-Regular;
    font-weight:400;
    color:rgba(155,155,155,1);
    line-height:16.5px;
  }
  .right {
    width: 70px;
    height: 61.5px;
    padding: 9px 0;
    margin-right: 15px;
  }
  .right img {
    width: 100%;
    height: 100%;
  }
</style>



